<script>
import DataReportService from "@/services/DataService";
import draggable from "vuedraggable";
import ReportPreview from '../Add_Report/ReportPreview.vue';
import CalculatedUI from "../../../components/Common/Modal/modalCalculatedFieldUI.vue";
import moment from 'moment'

export default {
    components: {
        draggable, ReportPreview, CalculatedUI
    },
    data() {
        return {
            isLoading: false,
            componentkey: 0,
            maincomponentkey: 0,
            getchangedinlist: 0,
            submodulecodefieldkey: 0,
            isFieldCollapsed: false,
            savedateformat: '',
            modulelisting: [],
            modulelistingforfilter: [],
            showModuleListForField: true,
            showModuleListForFilter: false,
            reqFrom: 'Reporting',
            submodulecode: null,
            modulename: null,
            maindiv: "col-6 pl-0 float-left",
            expandcollapsediv: "bottom_filter_button",
            showdiv: "mb-0 d-none",
            fielddata: [],
            Grouplist: [],
            Columnlist: [],
            Filterlist: [],
            viewId: null,
            ReportName: null,
            fieldpan: 'col-6 p-0 float-left mb-5',
            modulelistingmodel: null,
            customreportparameters: null,
            tempreportid: null,
            saveclass: "card d-none",
            editclass: "card",
            savereportname: '',
            Showsettingsview: false,
            showdiv1: false,
            dateformate: [],
            GetOperator: [],
            customField: null,
            dragcount: 1,
            templates: ['Match Any Value', 'Match All Value'],
            category: 'Match All Value',
            ReportPreviewListing: [],
            previewdata: [],
            PivotReportData: [],
            TotalSumOfColumnsData: [],
            itemclass: 'd-none',
            Groupsearchmodel: '',
            ColumnSearchmodel: '',
            MainSearchmodel: '',
            //filtergroup: [],
            submodulename: [],
            submodulecodename: [],
            test1: [],
            PageNumber: 1,
            PAGESIZE: 10,
            selectvalue: '',
            selecteddatatype: '',
            texttypevalue1: false,
            numbertypevalue1: false,
            datetypevalue1: false,
            datetypevalue2: false,
            timetypevalue: false,
            timetypevalue2: false,
            multiselectvalue: false,
            numbertypevalue2: false,
            texttypevalue2: false,
            savedOperatorName: null,
            drag: false,
            GetDateFromDB: [],
            modeldisplayname: null,
            modeldateformat: '',
            modeldecimalplaces: null,
            modeluseseparator: null,
            modelusecurrency: null,
            FilterCount: 1,
            valueTypeText: '',
            valueTypeNumber: '',
            valueTypeNumber2: '',
            valueTypeFirstdate: '',
            valueTypeSeconddate: '',
            Displaymultiplevalue: '',
            displayvalue2: '',
            ValueDisplaytext: '',
            ValueDisplaytext2: '',
            ValueDisplaynumber: '',
            ValueDisplaynumber2: '',
            ValueDisplaydate: '',
            ValueDisplaydate2: '',
            modeldaterange: '',
            ValueDisplayTime: '',
            ValueDisplayTime2: '',
            selectedvalue: '',
            RequestFilterJosn: [],
            CurrentOperatorSelected: null,
            filtertype: null,
            operator_id: null,
            mode: 'tags',
            dropdwonvalues: [],
            options: [],
            searchable: true,
            createOption: true,
            SelectedoperatorMappedDatatype: '',
            commaSepratedSelectedValues: '',
            selectedOPValue: '',
            GraphJson: [],
            sortingArray: null,
            graphData: null,
            showReportPreview: false,
            editjsondata: false,
            valueKey: '',
            valuekey2: '',
            FirstOperand: '',
            FirstOperandValue1: '',
            SecondOperand: '',
            SecondOperandValue1: '',
            Datatype: '',
            operator: '',
            DisplayName: '',
            showcalculatedUI: false,
            CheckExpressionFeild: false,
            ExpressionArray: [],
            ExpressionFieldsArray: [],
            saveJsonReport: [],
            editexpressionvalue: false,
            CalculatedUIEnabled: false,
            searchColumnfilter: [],
            LevelOfGrouping: null,
            ExpFieldDataArray: [],
            ExpDataArray: [],
            setvalue: 0,
            ChartGroupingArray: [],
            GroupingArray: [],
            PdfJsonReport: [],
            FilterActiveForMandatory: false,
            SearchNoRecordForField: false,
            SearchNoRecordForFilter: false,
            IsMandatoryforFilterAfterReset: false,
            ArrayforFilterpopup: [],
            modelFilterDDLValue: '',
            DDLForFilter: false,
            DDLForDateRange: false,
            selctRuleForFilter: '',
            IsForPreview: 1,
            IsDDLValueInFilter: false,
            GetSubModuleList: [],
            showRowLevelFunction: false,
            sortingresetvalue: false,
            AlreadyExistExpression: {},
            isExpressionSeprator: false,
            isExpressionCurrencySymbol: false,
            ExpressionKey: null,
            ColumnlistArray: [],
            DateRangeData: [],
            GetSelectedValue: '',
            GrouplistIndex: null,
            isSaveEnabled: false,
            FormSchema: [
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "MultiSelectField",
                            label: '',
                            name: "Select",
                            value: "",
                            mode: "tag",
                            placeholder: "Select",
                            config: {
                                options: [],
                                showAddIcon: false,
                            },
                            validationRules: "",
                        },
                    ]
                }
            ],
            FieldLimitvalue: 0,
            modeluseLinegraph: false,
            totalProgressValue: '',
            DropDownDecimalObj: [],
            ParentTotalValue: '',
            selectedOption: 'textbox',
            IsReportType: 1,
            Ispivotvalue: 1,
            RowList: [],
            currentPivotElement: [],
            showfieldSettingsUI: false,
            PivotColumnSearchmodel: '',
            PivotColumnList: [],
            filteredPivotColumnList: [],
            PivotValueSearchmodel: '',
            PivotValueList: [],
            filteredPivotValueList: [],
            FormSchemaa: [
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "RadioField",
                            showLabel: false,
                            name: "PivotFunction",
                            value: "",
                            placeholder: "PivotFunction",
                            validationRules: {
                                "required": false
                            },
                            config: {
                                options: []
                            },
                            disabled: false,
                        },
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                }
            ],
            PivotValueArray: [],
            currentPopupIndex: null,
            formKey: 0
        }
    },
    watch: {
        totalProgressValue(newVal) {
            if (newVal) {
                this.ParentTotalValue = undefined;
            }
        },
        ParentTotalValue(newVal) {
            if (newVal !== undefined && newVal !== null) {
                this.totalProgressValue = '';
            }
        }
    },
    created: async function () {
        var vm = this;
        $(".fade").addClass("show");
        if (this.$route.params.Modulename != undefined) {
            vm.customreportparameters = this.$route.params;
            vm.storeParameters();
        } else { vm.GetStoredParameters(); }
        await vm.GetList(vm.customreportparameters);
        if (vm.customreportparameters.ReportID != null || vm.customreportparameters.ReportID != undefined) {
            await vm.GetReportDetailsByReportId();
            if (vm.IsDDLValueInFilter) {
                setTimeout(function () {
                    vm.showReportPreview = true;
                }, 1000)
            } else { vm.showReportPreview = true; }
        }
        vm.filterRulechange();
        vm.GetDefaultCurrency();
    },
    computed: {
        filtergroup() {
            var vm = this;
            return vm.Columnlist.filter(item => {
                var displayname = (vm.$t(item.DisplayName != null)) ? vm.$t(item.DisplayName) : vm.$t(item.FieldName);
                return displayname.replace(/\W/g, "").toLowerCase().includes(vm.Groupsearchmodel.replace(/\W/g, "").toLowerCase());
            })
        },
        filterColumngroup() {
            var vm = this;
            var searchColumn = [];
            var searchfilter = [];
            var GetSubModuleName = null;
            vm.modulelisting.forEach((item, index) => {
                vm.modulelisting[index].forEach((indexitem) => {
                    searchfilter.push(indexitem);
                    searchColumn = searchfilter.filter(searchitem => {
                        var displaycolname = (vm.$t(searchitem.DisplayName != null)) ? vm.$t(searchitem.DisplayName) : vm.$t(searchitem.FieldName);
                        // var displaycolname = (searchitem.DisplayName != null) ? searchitem.DisplayName : searchitem.FieldName;
                        return displaycolname.replace(/\W/g, "").toLowerCase().includes(vm.ColumnSearchmodel.replace(/\W/g, "").toLowerCase());
                    })
                    searchColumn.forEach(SearchItem => {
                        GetSubModuleName = vm.modulelisting[index].find(x => x.CustomFieldId === SearchItem.CustomFieldId)
                        if (GetSubModuleName != undefined) {
                            GetSubModuleName.MainFieldname = vm.submodulename[index];
                        }
                    })
                })
            })
            return searchColumn;
        }
    },
    methods: {
        CheckReportType(val) {
            var ths = this;
            ths.IsReportType = val ? 1 : 0;
        },
        PivotGroupSearching: function () {
            this.itemclass = '';
        },
        PivotRowSearching() { },
        PivotColumnSearching() {
            const search = this.PivotColumnSearchmodel.toLowerCase();
            this.filteredPivotColumnList = this.submodulecodename.flatMap(item =>
                item.CustomFields.filter(field =>
                    field.ModuleName?.toLowerCase().includes(search) ||
                    field.DisplayName?.toLowerCase().includes(search) ||
                    field.FieldName?.toLowerCase().includes(search)
                )
            );
        },
        PivotselectfilterColumnvalue(item) {
            const exists = this.PivotColumnList.some(col => col.CustomFieldId === item.CustomFieldId);
            if (exists) {
                return;
            }
            this.PivotColumnList.push(item);
            this.PivotColumnSearchmodel = '';
            this.filteredPivotColumnList = [];
        },
        DeletePivotColumnFields(item, event, index) {
            this.PivotColumnList.splice(index, 1);
        },
        PivotValueSearching() {
            debugger
            const search = this.PivotValueSearchmodel.toLowerCase();
            this.filteredPivotValueList = this.submodulecodename.flatMap(item =>
                item.CustomFields.filter(field =>
                    field.ModuleName?.toLowerCase().includes(search) ||
                    field.DisplayName?.toLowerCase().includes(search) ||
                    field.FieldName?.toLowerCase().includes(search)
                )
            );
        },
        Pivotselectfiltervalue(item) {
            debugger
            const exists = this.PivotValueList.some(col => col.CustomFieldId === item.CustomFieldId);
            if (exists) {
                return;
            }
            const newItem = { ...item };
            this.$set(newItem, 'IsShow', false);
            this.$set(newItem, 'ColumnFieldDataCode', item.dttype[0]?.DataTypeCode || '');
            this.PivotValueList.push(newItem);
            this.PivotValueSearchmodel = '';
            this.filteredPivotValueList = [];
        },
        DeletePivotValueFields(item, event, index) {
            debugger
            this.PivotValueList.splice(index, 1);
            const pivotArrayIndex = this.PivotValueArray.findIndex(x => x.CustomFieldId === item.CustomFieldId);
            if (pivotArrayIndex !== -1) {
                this.PivotValueArray.splice(pivotArrayIndex, 1);
            }
        },
        OpenValueExpression(ele, evt) {
            debugger
            var vm = this;
            vm.PivotValueList.forEach((item, i) => {
                vm.$set(this.PivotValueList[i], 'IsShow', false);
            });

            const index = this.PivotValueList.findIndex(x => x.CustomFieldId === ele.CustomFieldId);
            if (index !== -1) {
                vm.currentPopupIndex = index;
                ele.IsShow = true;
                vm.$set(this.PivotValueList[index], 'IsShow', true);
                const countOnlyOption = [{ value: 'COUNT', name: 'COUNT' }];
                const fullOptions = [
                    { value: 'COUNT', name: 'COUNT' },
                    { value: 'SUM', name: 'SUM' },
                    { value: 'AVG', name: 'AVG' },
                    { value: 'MIN', name: 'MIN' },
                    { value: 'MAX', name: 'MAX' }
                ];
                if (ele.ColumnFieldDataCode === 'string') {
                    vm.FormSchemaa[0].Data[0].config.options = countOnlyOption;
                } else {
                    vm.FormSchemaa[0].Data[0].config.options = fullOptions;
                }
                if (!ele.PivotFunction) {
                    ele.PivotFunction = vm.FormSchemaa[0].Data[0].config.options[0].value;
                }
                if (ele.PivotFunction != '') {
                    vm.FormSchemaa[0].Data[0].value = ele.PivotFunction;
                }
                vm.formKey++;
            }
        },
        CloseExpressionPopup() {
            debugger
            var vm = this;
            vm.PivotValueList.forEach((item, i) => {
                vm.$set(this.PivotValueList[i], 'IsShow', false);
            });
            vm.currentPopupIndex = null;
        },
        onSubmit(e) {
            debugger
            var vm = this;
            if (vm.currentPopupIndex !== null) {
                const element = this.PivotValueList[this.currentPopupIndex];
                vm.$set(vm.PivotValueList[vm.currentPopupIndex], 'PivotFunction', e.PivotFunction);
                vm.$set(vm.PivotValueList[vm.currentPopupIndex], 'IsShow', false);
                vm.PivotValueArray = [];
                vm.PivotValueList.forEach((itm => {
                    debugger
                    var Obj = {
                        CustomFieldId: itm.CustomFieldId,
                        FieldName: itm.FieldName,
                        DisplayName: itm.DisplayName,
                        PivotFunction: itm.PivotFunction
                    }
                    vm.PivotValueArray.push(Obj);
                }))
                vm.currentPopupIndex = null;
            }

        },
        DeleteRowFields: function (element, event, index) {
            var vm = this;
            const rowIndex = vm.RowList.findIndex(obj => obj.CustomFieldId === element.CustomFieldId);
            if (rowIndex !== -1) {
                vm.RowList.splice(rowIndex, 1);
            }
            if (element.ModuleIndex !== undefined) {
                vm.modulelisting[element.ModuleIndex].push({
                    ...element,
                });
            }
        },
        GetDefaultCurrency: async function () {
            var vm = this
            await DataReportService.GetConfigurationValueForCompanyByKey('key=DynamicReportFieldCount').then((response) => {
                if (response && response.data && response.data.length > 0) {
                    vm.FieldLimitvalue = parseInt(response.data);
                }
            });
        },
        storeParameters() {
            var vm = this;
            localStorage.setItem("SavedParameters", JSON.stringify(vm.customreportparameters));
        },
        GetStoredParameters() {
            var vm = this;
            vm.customreportparameters = JSON.parse(localStorage.getItem("SavedParameters"));
        },
        async GetFilterSelectDDL(CustomFieldId) {
            debugger
            var vm = this;
            vm.options = [];
            var Getddldata = [];
            // vm.isLoading = false;
            // if (vm.operator_id == 10 || vm.operator_id == 27) {
            //     vm.isLoading = true;
            // }
            vm.isLoading = true;
            await DataReportService.GetFilterDDLselectvalue(CustomFieldId).then(response => {
                if (response.data.length > 0) {
                    // vm.options = response.data.map(item => {
                    //     return item.name != undefined ? item.name : item[FieldName]
                    // });
                    vm.FormSchema[0].Data[0].config.options = [];
                    Getddldata = response.data;
                    Getddldata.forEach(ddlitem => {
                        vm.options.push({
                            value: ddlitem.value,
                            name: ddlitem.name
                        })
                    })
                    vm.FormSchema[0].Data[0].config.options = vm.options;
                    vm.isLoading = false;
                } else { vm.isLoading = false; }
            }).catch(err => {
                console.log(err)
            })
        },
        async GetDateRangeFilterDDL() {
            var vm = this;
            await DataReportService.GetDateRangeFilterDDL().then(response => {
                vm.DateRangeData = JSON.parse(response.data[0].Value).Data;
            })
        },
        async GetReportDetailsByReportId() {
            debugger
            var vm = this;
            vm.isLoading = true;
            var params = `ReportId=${vm.customreportparameters.ReportID}&IsPaged=1`;
            await DataReportService.GetReportDetailsbyId(params).then(async response => {
                var checkfields = [];
                if (response.data != null && response.data != undefined && response.data != '') {
                    vm.IsReportType = response.data.JSON_To_Create_ReportListing[0].Info.TYPE == 'PIVOT' ? 0 : 1;
                    if (vm.IsReportType == '0') {
                        checkfields = response.data.JSON_To_Create_ReportListing[0].Info.PIVOTROW;
                    } else {
                        checkfields = response.data.JSON_To_Create_ReportListing[0].Info.FIELDS;
                    }
                }
                if (response.data != null && response.data != undefined && response.data != '' && checkfields.length != 0) {
                    if (vm.IsReportType == '0') {
                        debugger
                        vm.BindRowFeilds(response.data.JSON_To_Create_ReportListing[0].Info.PIVOTROW)
                        vm.PivotColumnList = response.data.JSON_To_Create_ReportListing[0].Info.PIVOTCOLUMN;
                        vm.PivotValueList = response.data.JSON_To_Create_ReportListing[0].Info.PIVOTValues;
                        await vm.BindFilters(response.data.JSON_To_Create_ReportListing[0].Info.FILTERS);
                        vm.PivotValueList.forEach((itm) => {
                            var Obj = {
                                CustomFieldId: itm.CustomFieldId,
                                FieldName: itm.FieldName,
                                DisplayName: itm.DisplayName,
                                PivotFunction: itm.PivotFunction
                            }
                            vm.PivotValueArray.push(Obj);
                        });
                        vm.sortingArray = response.data.JSON_To_Create_ReportListing[0].Info.SORTING;
                        vm.graphData = [];
                        vm.ExpressionFieldsArray = [];
                        vm.Grouplist = [];
                        vm.Columnlist = [];
                        vm.editexpressionvalue = false;
                        if (response.data.ReportListing == 1) {
                            vm.editjsondata = true;
                            vm.saveJsonReport = response.data.JSON_To_Create_ReportListing[0];
                            vm.PdfJsonReport = response.data.JSON_To_Create_ReportListing[0];
                        }
                    }
                    else {
                        vm.BindFeilds(response.data.JSON_To_Create_ReportListing[0].Info.FIELDS);
                        await vm.BindFilters(response.data.JSON_To_Create_ReportListing[0].Info.FILTERS);
                        vm.BindGROUP(response.data.JSON_To_Create_ReportListing[0].Info.GROUP);
                        vm.sortingArray = response.data.JSON_To_Create_ReportListing[0].Info.SORTING
                        vm.graphData = response.data.JSON_To_Create_ReportListing[0].Info.GRAPH;
                        vm.ExpressionArray = response.data.JSON_To_Create_ReportListing[0].Info.EXPRESSION;
                        vm.ExpressionFieldsArray = response.data.JSON_To_Create_ReportListing[0].Info.EXPRESSIONFIELDSARRAY;
                        vm.editexpressionvalue = true;
                        if (response.data.ReportListing == 1) {
                            vm.editjsondata = true;
                            vm.saveJsonReport = response.data.JSON_To_Create_ReportListing[0];
                            vm.PdfJsonReport = response.data.JSON_To_Create_ReportListing[0];
                        }
                    }
                }
                else {
                    if (checkfields.length == 0) {
                        vm.CheckIsMandartoryForFilter();
                    }
                    vm.isLoading = false;
                }
            }).catch(err => {
                console.log(err);
            })
        },
        BindFeilds(FIELDS) {
            var vm = this;
            FIELDS.forEach((item) => {
                vm.Columnlist.push(item);
                vm.modulelisting.forEach((mitem, index) => {
                    let indexFound = vm.modulelisting[index].findIndex(element => { return element.CustomFieldId === item.CustomFieldId })
                    if (indexFound != -1) {
                        vm.modulelisting[index].splice(indexFound, 1);
                    }
                })
            })
            if (vm.Columnlist.length > 0) {
                vm.CalculatedUIEnabled = true;
            }
        },
        BindRowFeilds(FIELDS) {
            var vm = this;
            FIELDS.forEach((item) => {
                vm.RowList.push(item);
                vm.modulelisting.forEach((mitem, index) => {
                    let indexFound = vm.modulelisting[index].findIndex(element => { return element.CustomFieldId === item.CustomFieldId })
                    if (indexFound != -1) {
                        vm.modulelisting[index].splice(indexFound, 1);
                    }
                })
            })
        },
        bindpouupvaluefordatetime(item) {
            var vm = this;
            if (item.ColumnFieldDataCode == 'datetime' || item.ColumnFieldDataCode == 'date') {
                if (item.operator_display_name == 'Between') {
                    vm.valueKey = 'valueTypeFirstdate'
                    vm.valuekey2 = 'valueTypeSeconddate'
                }
                else if (item.operator_display_name == 'Date Range') {
                    vm.valueKey = 'DateRangeValue'
                }
                else {
                    vm.valueKey = 'valueTypeFirstdate'
                }
            }
            if (item.ColumnFieldDataCode == 'time') {
                if (item.operator_display_name == 'Between') {
                    vm.valueKey = 'ValueDisplayFirstTime'
                    vm.valuekey2 = 'ValueDisplaySecondTime'
                }
                else {
                    vm.valueKey = 'ValueDisplayFirstTime'
                }
            }
        },
        bindfilterdata(FILTERS) {
            debugger
            var vm = this;
            vm.Filterlist = FILTERS.map((item, index) => {
                var GetDDLValueinEdit = '';
                var GetDDLTempVal = '';
                if (item.ColumnFieldDataCode == 'select' && (item.PickListOption == 'Lookup' || item.PickListOption != 'Lookup')) {
                    var GetDDLValueinEditArr = [];
                    if (item.OperatorExpression.includes('IN') || item.OperatorExpression.includes('NOT IN')) {
                        var GetDDLValueinEditMulti = [];
                        GetDDLValueinEditArr = item.Value1.split(',');
                        GetDDLValueinEditArr.forEach(DDLEditItem => {
                            GetDDLValueinEdit = vm.options.find(i => i.value === DDLEditItem.replace(/"|'/g, ''))
                            GetDDLValueinEditMulti.push(GetDDLValueinEdit.name);
                        })
                        GetDDLTempVal = GetDDLValueinEditMulti?.join();
                        vm.valueKey = 'Displaymultiplevalue';
                    }
                    else {
                        GetDDLValueinEdit = vm.options.find(i => i.value === item.Value1);
                        GetDDLTempVal = GetDDLValueinEdit?.name;
                        vm.valueKey = 'DropDownValue';
                    }
                }
                else if (item.ColumnFieldDataCode == 'decimal' || item.ColumnFieldDataCode == 'bigint' || item.ColumnFieldDataCode == 'int') {
                    if (item.operator_display_name == 'Between') {
                        vm.valueKey = 'valueTypeNumber';
                        vm.valuekey2 = 'valueTypeNumber2';
                    }
                    else {
                        vm.valueKey = 'valueTypeNumber';
                    }
                }
                else if (item.ColumnFieldDataCode == 'string') {
                    if (item.OperatorExpression.includes('IN') || item.OperatorExpression.includes('NOT IN')) {
                        vm.valueKey = 'valueTypeText';
                        vm.valuekey2 = 'valueTypeText2';
                    } else { vm.valueKey = 'valueTypeText' };
                }
                vm.bindpouupvaluefordatetime(item);
                let moduleIndex = vm.modulelistingforfilter.findIndex(moduleItem => {
                    return moduleItem.find(field => {
                        return field.CustomFieldId == item.CustomFieldId
                    })
                })
                let indexFound = vm.modulelistingforfilter[moduleIndex].findIndex(field => { return field.CustomFieldId == item.CustomFieldId })
                let returnObject = {}
                if (indexFound != -1) {
                    returnObject = {
                        FieldName: vm.modulelistingforfilter[moduleIndex][indexFound].FieldName,
                        CustomFieldId: item.CustomFieldId,
                        ModuleIndex: moduleIndex,
                        ActualDataType: item.ActualDataType,
                        FilterCount: index + 1,
                        FilterTypeAndorOr: vm.filtertype,
                        OPERATOR_ID: item.OPERATOR_ID,
                        OperatorDisplayName: item.operator_display_name,
                        IsMandatoryforFilter: item.IsMandatoryforFilter,
                        FieldIcons: item.FieldIcons,
                        DisplayName: item.DisplayName,
                        ColumnFieldDataCode: item.ColumnFieldDataCode,
                        PickListOption: item.PickListOption,
                        Calculated_Field: false
                    }
                    returnObject[vm.valuekey2] = item.Value2;
                    // returnObject[vm.valueKey] = GetDDLTempVal == '' ? item.Value1.replace(/"|'/g, '') : GetDDLTempVal;  //Apostrophe Fixed
                    returnObject[vm.valueKey] = GetDDLTempVal == '' ? item.Value1 : GetDDLTempVal;
                    vm.modulelistingforfilter[moduleIndex].splice(indexFound, 1);
                }
                return returnObject;
            })
        },
        CheckforIncludeDataMatchesForFilter() {
            var vm = this;
            if (vm.filtertype == 'AND') {
                vm.category = 'Match All Value';
            } else {
                vm.category = 'Match Any Value'
            }
        },
        BindFilters: async function (FILTERS) {
            debugger
            var vm = this;
            if (FILTERS.length > 0) {
                FILTERS.map(async (item, index) => {
                    if (item.ColumnFieldDataCode == 'select' && item.PickListOption == 'Lookup') {
                        vm.IsDDLValueInFilter = true;
                        await vm.GetFilterSelectDDL(item.CustomFieldId);
                    }
                    else if (item.ColumnFieldDataCode === 'select' && item.PickListOption !== 'Lookup') {

                        vm.FormSchema[0].Data[0].config.options = [];
                        vm.options = [];

                        if (item.PickListOption) {
                            // Split the comma-separated string into an array
                            const pickListArray = item.PickListOption.split(',');

                            // Loop through and create option objects
                            pickListArray.forEach(item => {
                                vm.options.push({
                                    value: item.trim(),
                                    name: item.trim()
                                });
                            });
                        }

                        // Assign to dropdown config
                        vm.FormSchema[0].Data[0].config.options = vm.options;

