                return false;
            }
            vm.editclass = "card";
            vm.saveclass = "card d-none";
            if (vm.savereportname.trim() != '') {
                var params = `ReportName=${vm.savereportname.trim()}&Report_Id=${vm.customreportparameters.ReportID}&Folder_Id=${vm.customreportparameters.Folder_Id}&Module_Id=${vm.customreportparameters.Module_Id}&Sub_Module_Id=${vm.customreportparameters.Sub_Module_Id}&Ref_Code=${vm.customreportparameters.Submodulecode}&Version=${vm.customreportparameters.Version}`;
                DataReportService.AddDynamicreport(params).then(response => {
                    if (response.data[0].Status == 'Update') {
                        vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("ReportName")]), 'success', true, vm.$t('Alert'));
                        vm.ReportName = vm.savereportname.trim();
                    } else if (response.data[0].Status == 'Exists') {
                        vm.savereportname = '';
                        vm.ShowAlert(vm.$t("AlreadyExists", [vm.$t("ReportName")]), 'failure', true, vm.$t('Alert'));
                    }
                }).catch(err => { console.log(err); })
            } else {
                vm.ShowAlert(vm.$t("ValidInput"), 'failure', true, vm.$t('Alert'));
            }
        },
