        SaveSelectedData: function (element, event) {

            var vm = this;
            var elem = vm.Columnlist.find(x => x.CustomFieldId == element.CustomFieldId);
            elem.OrginalDisplayName = elem.DisplayName
            elem.DisplayName = vm.modeldisplayname;
            elem.DateFormat = vm.modeldateformat;
            elem.DecimalPlace = vm.modeldecimalplaces;
            elem.IsUseSeprator = vm.modeluseseparator;
            elem.IsCurrencySymbol = vm.modelusecurrency;
            vm.savedateformat = vm.modeldateformat;
            vm.getchangedinlist += 1;
            let CheckSameNameExist = vm.MatchIfSameNameExist(undefined, element);
            if (!CheckSameNameExist) {
                if (vm.Grouplist.find(x => x.CustomFieldId === elem.CustomFieldId)) {
                    vm.Grouplist = [];
                    vm.Grouplist.push(elem);
                }
                vm.CloseSetting(event);
            } else {
                let GetChangedNameID = vm.MatchIfSameNameExist('GetChangedNameID', element);
                vm.modulelisting.forEach((mitem, mindex) => {
                    vm.GetSubModuleList[mindex].forEach(iitem => {
                        if (iitem.CustomFieldId === GetChangedNameID) {
                            var elem = vm.Columnlist.find(x => x.CustomFieldId == iitem.CustomFieldId);
                            elem.DisplayName = iitem.DisplayName;
                            elem.DateFormat = iitem.DateFormat;
                            elem.DecimalPlace = iitem.DecimalPlace;
                            elem.IsUseSeprator = iitem.IsUseSeprator;
                            elem.IsCurrencySymbol = iitem.IsCurrencySymbol;
