                        })
                    }
                }
                if (vm.sortingArray != null && vm.sortingArray.length > 0) {
                    let sortindex = vm.sortingArray.findIndex(x => x.CustomFieldId === element.CustomFieldId)
                    let getsortvalue = vm.sortingArray.find(x => x.CustomFieldId === element.CustomFieldId);
                    if (sortindex > -1 && getsortvalue != undefined) {
                        vm.sortingArray.splice(sortindex, 1);
                    }
                    vm.$refs.reportdata?.RemoveSortingForm({ sortcolummnId: element.CustomFieldId });
