        AddFilter: function (element, index, event) {
            var vm = this;
            vm.filterRulechange();
            let isFilterActive = vm.$refs.Filters.active != false ? vm.$refs.Filters.active : vm.FilterActiveForMandatory;
            if (vm.Filterlist.length < 20) {
                if (isFilterActive) {
                    const checkAlredyExist = vm.Filterlist.findIndex(object => object.CustomFieldId === element.CustomFieldId);
                    if (checkAlredyExist === -1) {
                        if (vm.category == 'Match All Value') {
                            vm.filtertype = 'AND'
                        }
                        else {
                            vm.filtertype = 'OR'
                        }
                        vm.Filterlist.push({
                            CustomFieldId: element.CustomFieldId,
                            ModuleIndex: index,
                            FieldName: element.FieldName,
                            DisplayName: element.DisplayName,
                            ActualDataType: element.ActualDataType,
