                            // valueTypeText: ((typeof (element.result) === 'undefined') ? null : element.result),
                            Calculated_Field: false
                        });
                    }
                    if (event.removed == undefined) {
                        vm.modulelistingforfilter[index].splice(vm.modulelistingforfilter[index].indexOf(element), 1);
