<template>
    <div>
        <div class="right-content datareport hr_jobpost">
            <div class="col-md-12 p-0 mb-2">
                <div class="theme-primary partition p-actions-expand overflow-visible">
                    <loader :isVisible="isLoading"></loader>
                    <span class="p-name text-white">{{ $t('ReportPreview') }}</span>
                    <span class="p-actions float-right mydbsize">
                        <a :class="Columnlist.length > 0 || RowList.length > 0 ? 'p-action-btn text-white dash' : 'p-action-btn text-white dash disabled'"
                            data-original-title="REPORT PREVIEW" v-on:click="PreviewReport">
                            <em class="fa fa-play"></em><br>{{ $t('ReportPreview') }}
                        </a>
                        <a class="p-action-btn text-white dash" data-original-title="RESET" v-on:click="ResetData">
                            <em class="fa fa-repeat"></em><br>{{ $t('Reset') }}
                        </a>
                        <a class="p-action-btn text-white dash" data-original-title="PDF" v-on:click="ExportToPDF">
                            <em class="fa fa-file-pdf-o"></em><br>{{ $t('ExportToPdf') }}
                        </a>
                        <a class="p-action-btn text-white dash" data-original-title="Excel" v-on:click="ExportToExcel">
                            <em class="fa fa-file-excel-o"></em><br>{{ $t('ExportToExcel') }}
                        </a>
                        <a v-on:click="OpenSortingPopup" class="p-action-btn text-white dash"
                            data-original-title="SORT">
                            <em class="fa fa-sort-amount-asc"></em><br>{{ $t('SORT') }}
                        </a>
                        <a v-if="ReportType == '1'" v-on:click="OpenGraph" class="p-action-btn text-white dash"
                            data-original-title="GRAPH">
                            <em class="fa fa-pie-chart"></em><br>{{ $t('Graph') }}
                        </a>
                        <div class="parentdiv datarepfix2">
                            <div class="verticle-action lft75 rt30">
                                <span class="action-list-box small" style="width:400px">
                                    <span id="action-list1"
                                        class="text-left p-0 datarep mt-3 w-90 float-left bg_outerdark borderbababa position-relative"
                                        v-if="isSortPopup">
                                        <div class="float-left col-12">
                                            <div class="container-fluid p-0 mb-3">
                                            </div>
                                            <div class="row px-3 pb-0" v-for="(input, index) in sortColummnarray"
                                                :key="`phoneInput-${index}`">
                                                <div class="col-lg-5 pl-0 pr-3">
                                                    <div class="form-group">
                                                        <label>{{ $t('SortBy') }}</label>
                                                        <select v-model="input.sortcolummnId" class="form-control"
                                                            v-if="ReportType == '1'">
                                                            <option value="" selected disabled>Select Field</option>
                                                            <option
                                                                v-for="item in Columnlist.filter(x => x.Calculated_Field == false)"
                                                                :value="item.CustomFieldId" :key="item.CustomFieldId"
                                                                :disabled="(sortColummnarray.filter(x => x.sortcolummnId == item.CustomFieldId)).length > 0">
                                                                {{ item.DisplayName ?
                                                                    GetLocalizedValue(item.DisplayName) :
                                                                    GetLocalizedValue(item.FieldName) }}
                                                            </option>
                                                        </select>
                                                        <select v-model="input.sortcolummnId" class="form-control"
                                                            v-else>
                                                            <option value="" selected disabled>Select Field</option>
                                                            <option
                                                                v-for="item in RowList.filter(x => x.Calculated_Field == false)"
                                                                :value="item.CustomFieldId" :key="item.CustomFieldId"
                                                                :disabled="(sortColummnarray.filter(x => x.sortcolummnId == item.CustomFieldId)).length > 0">
                                                                {{ item.DisplayName ?
                                                                    GetLocalizedValue(item.DisplayName) :
                                                                    GetLocalizedValue(item.FieldName) }}
                                                            </option>
                                                        </select>
                                                    </div>
                                                </div>
                                                <div class="col-lg-5 pl-0 pr-3">
                                                    <div class="form-group">
                                                        <label>{{ $t('OrderBy') }}</label>
                                                        <select v-model="input.columnOrders" class="form-control">
                                                            <option value="" selected disabled>Select Order</option>
                                                            <option>{{ $t('ASC') }}</option>
                                                            <option>{{ $t('DESC') }}</option>
                                                        </select>
                                                    </div>
                                                </div>
                                                <div class="col-lg-2">
                                                    <div class="form-group">
                                                        <a v-on:click="AddSortingForm(input)"><em
                                                                class="fa fa-plus-circle font-24 text-success mt-3"></em></a>
                                                        <a v-show="sortColummnarray[0].sortcolummnId != '' || isValueExistInSort"
                                                            v-on:click="RemoveSortingForm(input)"><em
                                                                class="fa fa-times-circle font-24 text-danger mt-3 ml-1"></em></a>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                        <div class="va_lft25 top4"><em
                                                class="icon-solid arrow-solid-left text-secondary"
                                                style="font-size: 20pt;"></em>
                                        </div>
                                        <button type="button" class="close closebtnsmall" v-on:click="close()">
                                            <em aria-hidden="true" class="fa fa-times"></em>
                                        </button>
                                    </span>
                                </span>
                            </div>
                        </div>

                    </span>
                    <span class="collapse-head-btns">
                        <a href="javascript:;" class="collapse-btns">
                            <em class="fa fa-angle-up text-white"></em>
                        </a>
                    </span>
                </div>

            </div>

            <div class="col-md-12 p-0 mb-2">
                <div class="tab-pane mb-0">
                    <div class="tab-content fsmtabcontrolMobileView" id="myTabContent">
                        <div class="expand-list">
                            <a href="javascript:;" class="expand-list-btn justify-content-center align-items-center">
                                <em class="icon-solid arrow-solid-left text-white" style="font-size:9pt"></em>
                            </a>
                        </div>
                        <div v-if="showGraphOptions" class="card mb-3 probx">
                            <div class="card-header">
                                <h2>{{ $t('Graph') }} </h2>
                                <a class="btn btn-primary" v-on:click="OpenGraphOptions">{{ $t('GraphOptions') }} <em
                                        class="fa fa-cogs"></em></a>
                            </div>
                            <div v-if="showChart" class="card-body" id="Reportpdf" style="min-height: 245px;">
                                <div class="float-right"><a class="text-center text-danger"
                                        v-on:click="ResetGraphData()">
                                        <span class="fa fa-trash-o text-danger" style="font-size:25px;"></span>
                                    </a></div>
                                <div class="row">
                                    <div class="col-md-12 col-lg-12 text-center p-0">
                                        <div class="border bg-light float-left text-left p-0  graphinfo_base_lft">
                                            <ul class="graphinfo">
                                                <li><label>X-axis:</label>
                                                    <!-- {{ GetLocalizedValue(FilterCustomFieldName(XAxisField)) }} -->
                                                    {{ XAxisFieldName }}
                                                </li>
                                                <li><label>Y-axis:</label>
                                                    <!-- {{ GetLocalizedValue(FilterCustomFieldName(YAxisField)) }} -->
                                                    {{ YAXisFieldName }}
                                                </li>
                                                <li v-show="aggregateFunctionResult != 0">
                                                    <label># {{ aggregateFunctionType }}:</label>
                                                    {{ aggregateFunctionResult }}
                                                </li>
                                            </ul>
                                        </div>
                                        <div class="p-0 m-0 float-left graphinfo_base_rgt"
                                            v-if="chartType == 'line' || chartType == 'bar'">
                                            <VueApexCharts ref="chart" :type="chartType" height="auto"
                                                :options="chartOptions" :series="chartSeries">
                                            </VueApexCharts>
                                        </div>
                                        <div class="p-0 m-0 float-left graphinfo_base_rgt"
                                            v-if="chartType == 'pie' || chartType == 'donut'">
                                            <VueApexCharts ref="chart" :type="chartType" height="auto"
                                                :options="chartOptions" :series="chartSeries">
                                            </VueApexCharts>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div v-if="isChartColor" class="card-body" id="Reportpdf" style="min-height: 245px;">
                                <div class="float-right"><a class="text-center text-danger"
                                        v-on:click="ResetGraphData()">
                                        <span class="fa fa-trash-o text-danger" style="font-size:25px;"></span>
                                    </a></div>
                                <div class="row">
                                    <div class="col-md-12 col-lg-12 text-center p-0">
                                        <div class="border bg-light float-left text-left p-0  graphinfo_base_lft">
                                            <ul class="graphinfo">
                                                <li><label>Y-axis:</label>
                                                    <!-- {{ GetLocalizedValue(FilterCustomFieldName(YAxisField)) }} -->
                                                    {{ YAXisFieldName }}
                                                </li>
                                                <li><label>Color-code:</label>{{ GetChartColorCode }}</li>
                                            </ul>
                                        </div>
                                        <span class="p-0 m-0 float-left graphinfo_base_rgt">
                                            <div class="col-sm-4 col-lg-4 px-0 bg-white mx-auto shadowder"
                                                style="min-height: 200px; padding-top: 75px;">
                                                <span class="ChartNumberClass">
                                                    <h1 :style="'color:' + GetChartColorCode">{{ GetChartColorData }}
                                                    </h1>
                                                </span>
                                            </div>
                                        </span>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="tab-pane p-0 pb-2 border-0 active" role="tabpanel" id="tab-detail-content"
                            aria-labelledby="tab-detail-content">
                            <div class="listing mt-3">
                                <div class="table-responsive table-fix-header">
                                    <table class="table table-bordered mt-0" style="width:100%" id="Reportpdf"
                                        aria-label="Report Preview Listing">
                                        <thead class="thead-bg">
                                            <tr>
                                                <th scope="col" v-for="(item, index) in previewgroupdata" :key="index"
                                                    v-if="showdata"
                                                    :class="{ 'text-right': item.ColumnFieldDataCode === 'decimal' || item.ColumnFieldDataCode === 'bigint' }">
                                                    {{ GetLocalizedValue((item.DisplayName != null && item.DisplayName
                                                        != '') ?
                                                        item.DisplayName : item.FieldName) }}</th>
                                                <th scope="col" v-for="(column, index) in previewcolumn" :key="index"
                                                    :class="{ 'text-right': column.ColumnFieldDataCode === 'decimal' || column.ColumnFieldDataCode === 'bigint' || column.ColumnFieldDataCode === null }">
                                                    {{ GetLocalizedValue((column.DisplayName != null &&
                                                        column.DisplayName != '') ? column.DisplayName : column.FieldName)
                                                    }}{{
                                                        column.ExpresssionFieldName }}
                                                </th>
                                            </tr>
                                        </thead>
                                        <tbody v-if="noRecord">
                                            <tr style="cursor:initial;">
                                                <td colspan="4">
                                                    <div
                                                        class="text-center text-danger d-flex align-items-center justify-content-center norecs">
                                                        {{ $t("NoRecordfound") }}</div>
                                                </td>
                                            </tr>
                                        </tbody>

                                        <tbody v-for="(item, index) in previewdata">
                                            <tr v-for="(sitem, sindex) in previewdata[index].data" v-if="showdata">
                                                <template v-if="sindex == 0">
                                                    <td class="breakspaces" v-for="(gitem, gindex) in previewgroupdata"
                                                        :style="(gitem.ColumnFieldDataCode === 'bigint' || gitem.ColumnFieldDataCode === 'decimal') ? 'text-align: right;' : ''"
                                                        :class="{ 'onlyrtbod': true, '': item.totalRecord == null }"
                                                        :rowspan="item.totalRecord">
                                                        {{ (gitem.ColumnFieldDataCode == 'bool' ?
                                                            item[gitem.DisplayName] == 1 || item[gitem.DisplayName] == true
                                                                ? 'Yes' : 'No'
                                                            : '' || gitem.ColumnFieldDataCode == 'bool' ?
                                                                item[gitem.FieldName] == 1 || item[gitem.FieldName] == true ?
                                                                    'Yes' : 'No' : '') ||
                                                            (item[gitem.DisplayName] != null && item[gitem.DisplayName] !=
                                                                '' ?
                                                                item[gitem.DisplayName] : item[gitem.FieldName]) }}

                                                        ({{ (item[gitem.DisplayName] != null && item[gitem.DisplayName]
                                                            != '' ||
                                                            gitem.FieldName != null && gitem.FieldName != '') ?
                                                            item.totalRecord
                                                            : '-' }})
                                                    </td>
                                                </template>
                                                <td v-for="(column, i) in previewcolumn" :key="i"
                                                    :style="(column.ColumnFieldDataCode === 'bigint' || column.ColumnFieldDataCode === 'decimal') ? 'text-align: right;' : ''">
                                                    <span
                                                        v-if="!(column.ColumnFieldDataCode == 'decimal' && column.IsLineGraph == true)"
                                                        class="breakspaces"
                                                        v-html="(column.ColumnFieldDataCode == 'bool' ? sitem[column.DisplayName] == 1 || sitem[column.DisplayName] == true ? 'Yes' : 'No'
                                                            : '' || column.ColumnFieldDataCode == 'bool' ? sitem[column.FieldName] == 1 || sitem[column.FieldName] == true ? 'Yes' : 'No' : '') ||

                                                            (sitem[column.DisplayName] === 0 ? '0' :
                                                                (sitem[column.DisplayName] != null && sitem[column.DisplayName] != '' ?
                                                                    sitem[column.DisplayName] : sitem[column.FieldName]))">
                                                    </span>

                                                    <span
                                                        v-if="column.ColumnFieldDataCode == 'decimal' && column.IsLineGraph == true"
                                                        class="overflow-visible pt-4"
                                                        style="width:242px;min-width:242px;">
                                                        <div class="progress">
                                                            <div class="progress-bar" role="progressbar" :style="SetWidthProgress(
                                                                sitem[column.DisplayName] === 0 ? 0 :
                                                                    (sitem[column.DisplayName] != null && sitem[column.DisplayName] !== ''
                                                                        ? sitem[column.DisplayName]
                                                                        : sitem[column.FieldName]),
                                                                column.TotalFieldValue != null && column.TotalFieldValue !== '' && column.TotalFieldValue !== undefined
                                                                    ? column.TotalFieldValue
                                                                    : sitem[column.ParentTotalValue]
                                                            )" :aria-valuenow="completionPercentage" aria-valuemin="0"
                                                                aria-valuemax="100">
                                                                {{ completionPercentage }}%
                                                            </div>
                                                        </div>

                                                    </span>
                                                </td>
                                            </tr>

                                            <tr v-if="showcoldata">

                                                <td v-for="(column, i) in previewcolumn" :key="i" :style="{
                                                    textAlign: (column.ColumnFieldDataCode === 'bigint' || column.ColumnFieldDataCode === 'decimal' || column.ColumnFieldDataCode == null) ? 'right' : '',
                                                    backgroundColor: column.FieldName === 'Overall Total' ? '#f0f0f0' : ''
                                                }">
                                                    <!-- <span
                                                        v-if="!(column.ColumnFieldDataCode == 'decimal' && column.IsLineGraph == true)"
                                                        class="breakspaces"
                                                        v-html="(column.ColumnFieldDataCode == 'bool' ?
                                                            item[column.DisplayName] == 1 || item[column.DisplayName] == true ? 'Yes' : 'No' : '' || column.ColumnFieldDataCode == 'bool' ?
                                                                item[column.FieldName] == 1 || item[column.FieldName] == true ? 'Yes' : 'No' : '') ||

                                                            (item[column.DisplayName] === 0 ? '0' :
                                                                (item[column.DisplayName] != null && item[column.DisplayName] != '' ?
                                                                    $n(item[column.DisplayName]) : $n(item[column.FieldName])))">
                                                    </span> -->

                                                    <span
                                                        v-if="!(column.ColumnFieldDataCode === 'decimal' && column.IsLineGraph === true)"
                                                        class="breakspaces" v-html="column.ColumnFieldDataCode === 'bool'
                                                            ? (item[column.DisplayName] == 1 || item[column.DisplayName] === true
                                                                ? 'Yes'
                                                                : 'No')
                                                            : (item[column.DisplayName] === 0
                                                                ? '0'
                                                                : (item[column.DisplayName] != null && item[column.DisplayName] !== ''
                                                                    ? (!['string', 'select'].includes(column.ColumnFieldDataCode) && ['decimal', 'bigint', null].includes(column.ColumnFieldDataCode)
                                                                        ? $n(item[column.DisplayName], 'decimal2')
                                                                        : item[column.DisplayName])
                                                                    : (item[column.FieldName] === 0
                                                                        ? '0'
                                                                        : (item[column.FieldName] != null && item[column.FieldName] !== ''
                                                                            ? (!['string', 'select'].includes(column.ColumnFieldDataCode) && ['decimal', 'bigint', null].includes(column.ColumnFieldDataCode)
                                                                                ? $n(item[column.FieldName], 'decimal2')
                                                                                : item[column.FieldName])
                                                                            : ''))))">
                                                    </span>


                                                    <span
                                                        v-if="column.ColumnFieldDataCode == 'decimal' && column.IsLineGraph == true"
                                                        class="overflow-visible pt-4"
                                                        style="width:242px;min-width:242px;">
                                                        <div class="progress">
                                                            <div class="progress-bar" role="progressbar" :style="SetWidthProgress(
                                                                item[column.DisplayName] === 0 ? 0 :
                                                                    (item[column.DisplayName] != null && item[column.DisplayName] !== ''
                                                                        ? item[column.DisplayName]
                                                                        : item[column.FieldName]),
                                                                column.TotalFieldValue != null && column.TotalFieldValue !== '' && column.TotalFieldValue !== undefined
                                                                    ? column.TotalFieldValue
                                                                    : item[column.ParentTotalValue]
                                                            )" :aria-valuenow="completionPercentage" aria-valuemin="0"
                                                                aria-valuemax="100">
                                                                {{ completionPercentage }}%
                                                            </div>
                                                        </div>
                                                    </span>
                                                    {{ item[column.ExpresssionFieldName] }}
                                                </td>


                                            </tr>
                                        </tbody>

                                        <tbody v-for="(Dataitem, Dataindex) in TotalSumOfColumnsData" :key="Dataindex"
                                            :style="{ backgroundColor: '#f0f0f0' }">
                                            <tr v-if="showcoldata">
                                                <td v-for="(columnData, columnindex) in previewcolumn"
                                                    :key="columnindex"
                                                    :style="(columnData.ColumnFieldDataCode == null) ? 'text-align: right;' : ''">
                                                    <span class="breakspaces" v-html="(Dataitem[columnData.FieldName] !== null && Dataitem[columnData.FieldName] !== '' && Dataitem[columnData.FieldName] !== undefined)
                                                        ? $n(Dataitem[columnData.FieldName], 'decimal2')
                                                        : Dataitem[columnData.FieldName]">
                                                    </span>

                                                </td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </div>
                        <div class="float-right">
                            <a :class="saveJsonReport.length != 0 && isSaveEnabled ? 'btn btn-success my-2 my-sm-0 mr-2' : 'btn btn-secondary my-2 my-sm-0 mr-2 disabled'"
                                v-on:click="SaveAllReport()">&emsp13;
                                <span class="fa fa-floppy-o"></span> {{ $t('Save') }}
                            </a>
                            <a class="btn btn-danger" v-on:click="CancelAll()">&emsp13;
                                <span class="fa fa-times"></span> {{ $t('Cancel') }}
                            </a>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <modalGraphOptions :Columnlist="Columnlist" :showGraphOptions="showGraphOptions"
            :customreportparameters="customreportparameters" :GraphData="GraphData" :sortColummnarray="sortColummnarray"
            :GraphJson="GraphJson" :getchangedinlist="getchangedinlist" ref="modalGraph">
        </modalGraphOptions>
    </div>
</template>
<script>
import ReportPreviewService from "@/services/DataService";
import modalGraphOptions from "@/components/Common/Modal/modalGraphOptions.vue";
import VueApexCharts from "vue-apexcharts";
import moment from "moment";
// import * as pdfFonts from "pdfmake/build/vfs_fonts.js"; // <-- vfs_fonts has to be imported before pdfmake
// import pdfMake from 'pdfmake';
const pdfFonts = require("pdfmake/build/vfs_fonts");
const pdfMake = require("pdfmake/build/pdfmake");

pdfMake.vfs = pdfFonts.pdfMake.vfs;

export default {
    components: {
        VueApexCharts,
        modalGraphOptions,
        pdfMake

    },
    props: {
        previewdata: {
            type: Array,
        },
        Columnlist: {
            type: Array
        },
        RowList: {
            type: Array
        },
        customreportparameters: {
            type: Object
        },
        Grouplist: {
            type: Array
        },
        saveJsonReport: {
            type: Object
        },
        ReportName: {
            type: String
        },
        GraphData: {
            type: Array
        },
        SortingData: {
            type: Array
        },
        GraphJson: {
            type: Array
        },
        editjsondata: {
            type: Boolean
        },
        LevelOfGrouping: {
            type: Number
        },
        PdfJsonReport: {
            type: Object
        },
        modeldateformat: {
            type: String
        },
        getchangedinlist: {
            type: Number
        },
        isSaveEnabled: {
            type: Boolean
        },
        PivotData: {
            type: Array
        },
        ReportType: {
            type: Boolean
        },
        TotalSumOfColumnsData: {
            type: Array
        }
    },
    data() {
        return {
            isLoading: false,
            previewcolumn: [],
            previewgroupdata: [],
            noRecord: true,
            showdata: false,
            showcoldata: true,
            showGraphOptions: false,
            showChart: false,
            showmulitGrouping: false,
            //Sorting PopUp
            isSortPopup: false,
            sortColummnarray: [{ sortcolummnId: "", columnOrders: "" }],
            DynamicFillValue: [],
            GroupValue: [],
            isValueExistInSort: false,
            isGraph: false,

            //Bind Chart 
            chartTypeID: null,
            chartType: "",
            chartSeries: [],
            chartOptions: {},
            aggregateFunctionType: null,
            aggregateFunctionResult: null,
            XAxisField: null,
            YAxisfield: null,
            watchvalue: false,
            allReocrdsList: null,
            ExcelData: null,
            BindGraph: false,
            SavedGraphData: {},
            GetChartColorCode: '',
            GetChartColorData: '',
            isChartColor: false,
            XAxisFieldName: '',
            YAXisFieldName: '',

            //Export to PDF
            allReocrdsData: null,
            TableBody: [],
            TableHeader: [],
            pdfContent: null,
            ExcelJsonData: null,
            count: 0,
            isPreviewForPdfExcel: 1,
            ClearXYAxis: '',
            ExDisplayName: '',
            IsExp: 0,
            completionPercentage: 0,
            ExtraFieldId: null

        }
    },
    created() {
        debugger
        var vm = this;
        if (vm.GraphData != null && vm.GraphData != undefined && vm.GraphData != '' && vm.GraphData.length > 0) {
            vm.showGraphOptions = true;
        }
        vm.EditSortingData();
        if (vm.previewdata != null && vm.previewdata != undefined && vm.previewdata.length > 0) {
            vm.RenderList();
        } if (vm.editjsondata) { vm.PreviewReport(); }
    },
    mounted() {
        debugger
        var vm = this;
        if (vm.GraphData != null && vm.GraphData != undefined && vm.GraphData != '' && vm.GraphData.length > 0) {
            vm.showGraphOptions = true;
            if (vm.aggregateFunctionType == '') {
                vm.aggregateFunctionType = vm.GraphData[0].AggregateFunction
            }
            if (vm.previewdata != null && vm.previewdata != undefined && vm.previewdata.length > 0) {
                vm.$refs.modalGraph?.ViewReportGraph();
            }
        }
    },
    watch: {
        watchvalue(newval, oldval) {
            var vm = this;
            if (newval != oldval) {
                vm.RenderList();
                vm.watchvalue = false;
            }
        }
    },
    methods: {
        SetWidthProgress(Value, TotalFieldValue) {
            var vm = this;
            if (Value != undefined && Value != null) {
                let completionPercentage = (Value / TotalFieldValue) * 100;
                vm.completionPercentage = completionPercentage.toFixed(0)
            }
            else {
                vm.completionPercentage = 0;
            }
            return {
                width: `${vm.completionPercentage}%`
            };
        },
        EditSortingData: function () {
            debugger
            var vm = this;
            if (vm.SortingData != null && vm.SortingData != undefined && vm.SortingData != '' && vm.SortingData.length > 0) {
                vm.sortColummnarray = [];
                vm.SortingData.forEach((item, index) => {
                    vm.sortColummnarray.push({
                        "sortcolummnId": item.CustomFieldId,
                        "columnOrders": item.SORTTYPE
                    })
                })
            }
            else {
                vm.sortColummnarray = [{ sortcolummnId: "", columnOrders: "" }]
            }
        },
        RefreshReport: function () {
            var vm = this;
            vm.showdata = false;
            vm.PreviewReport();
        },
        RenderList: function () {

            var vm = this;
            if (vm.previewdata != null && vm.previewdata != undefined && vm.previewdata.length > 0) {
                vm.previewcolumn = []
                var getkeys = Object.keys(vm.previewdata[0])
                if (vm.ReportType == '0') {
                    vm.RowList.forEach(x => {
                        vm.previewdata.forEach(item => {
                            if (x.ColumnFieldDataCode == 'date') {
                                var tempdata = item[x.DisplayName] != null ? item[x.DisplayName] : item[x.FieldName];
                                if (tempdata != undefined) {
                                    if (x.DateFormat == null) {
                                        var defaultdatecheck = moment(tempdata, "MM/DD/YYYY", true);
                                        if (item[x.DisplayName] != null && defaultdatecheck.isValid() == false) {
                                            item[x.DisplayName] = vm.$options.filters.formatDate(item[x.DisplayName])
                                        } else {
                                            item[x.FieldName] = vm.$options.filters.formatDate(item[x.FieldName])
                                        }
                                    }
                                }
                            }
                        })
                        vm.previewcolumn.push({ ...x });
                    })
                    vm.PivotData.forEach(item => {
                        vm.previewcolumn.push({
                            FieldName: item.value,
                            CustomFieldId: null,
                            Calculated_Field: null,
                            ColumnFieldDataCode: null,
                            IsLineGraph: null,
                            TotalFieldValue: null,
                            ParentTotalValue: null
                        });
                    })

                    vm.previewcolumn.push({
                        FieldName: "Overall Total",
                        CustomFieldId: null,
                        Calculated_Field: null,
                        ColumnFieldDataCode: null,
                        IsLineGraph: null,
                        TotalFieldValue: null,
                        ParentTotalValue: null
                    });
                    vm.noRecord = false;
                }
                else {
                    vm.Columnlist.forEach(x => {
                        vm.previewdata.forEach(item => {
                            if (x.ColumnFieldDataCode == 'date') {
                                var tempdata = item[x.DisplayName] != null ? item[x.DisplayName] : item[x.FieldName];
                                if (tempdata != undefined) {
                                    if (x.DateFormat == null) {
                                        var defaultdatecheck = moment(tempdata, "MM/DD/YYYY", true);
                                        if (item[x.DisplayName] != null && defaultdatecheck.isValid() == false) {
                                            item[x.DisplayName] = vm.$options.filters.formatDate(item[x.DisplayName])
                                        } else {
                                            item[x.FieldName] = vm.$options.filters.formatDate(item[x.FieldName])
                                        }
                                    }
                                }
                            }
                        })
                        vm.previewcolumn.push({ ...x });
                    })
                    vm.Grouplist.forEach(item => {
                        vm.previewcolumn.forEach((gitem, index) => {
                            if (item.DisplayName === gitem.DisplayName || item.FieldName === gitem.FieldName) {
                                vm.previewcolumn.splice(index, 1);
                            }
                        })
                    })
                    if (vm.previewcolumn != null && vm.previewcolumn != undefined && vm.previewcolumn.length > 0) {
                        if (vm.Grouplist.length > 0) {
                            vm.previewgroupdata = Object.assign([], vm.Grouplist);
                            vm.showdata = true;
                            vm.showcoldata = false;
                        }
                        else {
                            vm.showcoldata = true;
                            vm.previewgroupdata = [];
                        }
                        vm.noRecord = false;
                    }
                }
            }
            else {
                vm.previewcolumn = [];
                vm.previewgroupdata = [];
                vm.noRecord = true;
            }
        },
        PreviewReport: async function () {
            var vm = this;
            vm.isSortPopup = false;
            vm.BindGraph = false;
            if (vm.SavedGraphData.selectedChartTypeID != null && vm.SavedGraphData.selectedChartTypeID != undefined) {
                await vm.$refs.modalGraph.ApplyGraph();
                if (!vm.showGraphOptions) {
                    vm.OpenGraph();
                }
            }
            if (vm.sortColummnarray[0].sortcolummnId == undefined || vm.sortColummnarray[0].columnOrders == undefined) {
                vm.sortColummnarray = [{ sortcolummnId: "", columnOrders: "" }]
            }
            await vm.$parent.PreviewJson(vm.sortColummnarray, vm.BindGraph);
            vm.watchvalue = true
        },
        ResetData: function () {
            var vm = this;
            vm.showdata = false;
            vm.isSortPopup = false;
            vm.sortColummnarray = [{ sortcolummnId: "", columnOrders: "" }]
            vm.previewcolumn = [];
            vm.showGraphOptions = false;
            vm.showChart = false;
            vm.isChartColor = false;
            vm.chartType = null;
            vm.chartTypeID = null;
            vm.aggregateFunctionType = null;
            vm.aggregateFunctionResult = null;
            vm.XAxisField = null;
            vm.YAxisField = null;
            vm.chartSeries = [];
            vm.chartOptions = {};
            vm.GetChartColorCode = '';
            vm.GetChartColorData = '';
            vm.SavedGraphData = {};
            vm.$refs.modalGraph?.ResetGraph();
            vm.$parent.ResetPreview(vm.customreportparameters);
            vm.noRecord = true;
        },
        ResetGraphData: function () {
            var vm = this;
            vm.showGraphOptions = false;
            vm.$refs.modalGraph.ResetGraph();
            // vm.$refs.modalGraph.ApplyGraph();
            vm.chartType = ""
            vm.chartTypeID = null
            vm.aggregateFunctionType = null
            vm.aggregateFunctionResult = null
            vm.XAxisField = null
            vm.YAxisField = null
            vm.chartSeries = []
            vm.chartOptions = {}
            vm.GetChartColorCode = '';
            vm.GetChartColorData = '';
            vm.showChart = false;
            vm.SavedGraphData = {};
            vm.isChartColor = false;
            vm.BindGraph = false;
            vm.ClearXYAxis = '';
            vm.$children[1].XAxisFieldName = '';
            vm.$children[1].YAxisFieldName = '';
        },
        // NoDataForGraph: function (){
        //     var vm = this;
        //     vm.showGraphOptions = false;
        //     vm.chartType = ""
        //     vm.chartTypeID = null
        //     vm.aggregateFunctionType = null
        //     vm.aggregateFunctionResult = null
        //     vm.XAxisField = null
        //     vm.YAxisField = null
        //     vm.chartSeries = []
        //     vm.chartOptions = {}
        //     vm.showChart = false;
        //     vm.BindGraph = false;
        // },
        SaveAllReport: async function () {
            var vm = this;
            if (vm.editjsondata) {
                await vm.$parent.PreviewJson(vm.sortColummnarray);
            }

            if (vm.YAxisField != null && vm.YAxisField != undefined && vm.YAxisField != '') {
                var Obj = vm.Columnlist.find(item => item.CustomFieldId == vm.YAxisField);

                vm.ExDisplayName = Obj.ExpressionDisplay != null && Obj.ExpressionDisplay !== '' && Obj.ExpressionDisplay !== undefined ? Obj.DisplayName : '';
                vm.IsExp = Obj.ExpressionDisplay != null && Obj.ExpressionDisplay !== '' && Obj.ExpressionDisplay !== undefined ? 1 : 0;
            }

            var graphArray = {
                "chartTypeID": vm.chartTypeID,
                "XAxisField": vm.XAxisField,
                "YAxisField": vm.YAxisField,
                "ExtraFieldId": vm.ExtraFieldId,
                "AggregateFunction": vm.aggregateFunctionType,
                "AggregateFuncField": vm.GetChartColorData == '' ? vm.aggregateFunctionResult : vm.GetChartColorData,
                "ChartColorCode": vm.GetChartColorCode,
                "XAxisFieldName": vm.XAxisFieldName,
                "YAXisFieldName": vm.YAXisFieldName,
                "ExpDisplayName": vm.ExDisplayName,
                "IsExpression": vm.IsExp,
            }
            if (graphArray.chartTypeID != null) {
                if (vm.Grouplist.length <= 1) {
                    vm.Grouplist.forEach(item => {
                        vm.saveJsonReport.Info.GROUP.push(item);
                    })
                }
                vm.saveJsonReport.Info.GRAPH.push(graphArray);
            }
            var jsonData = JSON.stringify(vm.saveJsonReport);
            if (jsonData != '[]' && jsonData != undefined) {
                ReportPreviewService.SaveAllDynamicReport(jsonData).then((response) => {
                    if (response.data[0].Value == 1) {
                        vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("Report")]), 'success', true, vm.$t('Alert'));
                        this.$router.push({ name: 'CustomReports' });
                    }
                    else if (response.data[0].Value == 2) {
                        vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("Report")]), 'success', true, vm.$t('Alert'));
                        this.$router.push({ name: 'CustomReports' });
                    }
                }).catch((err) => {
                    console.log(err);
                })
            } else {
                vm.ShowAlert(vm.$t("No_Record_Found"), 'failure', true, vm.$t('Alert'));
                vm.isLoading = false;
            }
        },
        OpenSortingPopup: function () {
            var vm = this;
            if (!vm.isSortPopup) {
                vm.isSortPopup = true;
            }
            else {
                vm.isSortPopup = false;
            }
        },
        AddSortingForm: function (value) {
            debugger
            var vm = this
            // Add Sorting popup dynamically.
            if (vm.sortColummnarray[0].columnOrders != '' && vm.sortColummnarray[0].sortcolummnId != '' && vm.sortColummnarray.length < vm.Columnlist.length) {
                vm.sortColummnarray.push({ sortcolummnId: "", columnOrders: "" });
            } else {
                vm.ShowAlert(vm.sortColummnarray[0].columnOrders != '' && vm.sortColummnarray[0].sortcolummnId != '' ? vm.$t("SortLimitExceed") : vm.$t("AddFieldsFirst"), 'failure', true, vm.$t('Alert'));
            }
        },
        RemoveSortingForm: function (index) {
            var vm = this;
            if (vm.sortColummnarray.length == 1) {
                vm.sortColummnarray = [{ sortcolummnId: "", columnOrders: "" }]
            }
            else {
                // vm.sortColummnarray.splice(vm.sortColummnarray.indexOf(index), 1);
                let sortindex = vm.sortColummnarray.findIndex(x => x.sortcolummnId === index.sortcolummnId)
                if (sortindex > -1) {
                    vm.sortColummnarray.splice(sortindex, 1);
                }
                vm.isValueExistInSort = true;
            }
            if (vm.sortColummnarray.length == 1) {
                vm.isValueExistInSort = false
            }
        },
        OpenGraph: function () {
            var vm = this;
            vm.isSortPopup = false;
            if (!vm.showGraphOptions) {
                vm.showGraphOptions = true;
            }
            else {
                vm.showGraphOptions = false;
            }
        },
        OpenGraphOptions: function () {

            var vm = this;
            vm.showChart = false;
            vm.isChartColor = false;
            vm.isGraph = true;
            $("#CustomGraphOptions").modal('show');
            setTimeout(function () {
                $("#CustomGraphOptions").modal('show');
                setTimeout(function () {
                    vm.SetBootstraptoolTip();
                }, 200);
            }, 1000);
        },
        DrawGraph: function (graphParams, BindGraph) {
            var vm = this;
            if (graphParams.selectedChartTypeID != null && graphParams.selectedChartTypeID != undefined) {
                vm.SavedGraphData = graphParams;
                if (graphParams.ChartColorCode != '' && graphParams.selectedChartType == "numbers_chart") {
                    // vm.GetChartColorCode = graphParams.ChartColorCode;
                    // vm.GetChartColorData = graphParams.CharColorData;
                    // vm.YAxisField = graphParams.selectedChartYAxis;
                    // vm.aggregateFunctionType = graphParams.aggregateFunctionType;
                    // vm.chartTypeID = graphParams.selectedChartTypeID;
                    vm.isChartColor = true;
                    vm.showChart = false;
                } else {
                    // vm.chartType = graphParams.selectedChartType;
                    // vm.chartTypeID = graphParams.selectedChartTypeID;
                    // vm.aggregateFunctionType = graphParams.aggregateFunctionType;
                    // vm.aggregateFunctionResult = graphParams.aggregateFunctionResult;
                    // vm.XAxisField = graphParams.selectedChartXAxis;
                    // vm.YAxisField = graphParams.selectedChartYAxis;
                    // vm.chartSeries = graphParams.chartseries;
                    // vm.chartOptions = graphParams.chartoptions;
                    vm.showChart = true;
                    vm.isChartColor = false;
                }
                if (graphParams.chartoptions.legend != undefined) {
                    graphParams.chartoptions.legend.width = screen.availWidth - screen.availWidth * 69 / 100
                }
                vm.GetChartColorCode = graphParams.ChartColorCode;
                vm.GetChartColorData = graphParams.CharColorData;
                vm.chartType = graphParams.selectedChartType;
                vm.chartTypeID = graphParams.selectedChartTypeID;
                vm.aggregateFunctionType = graphParams.aggregateFunctionType;
                vm.aggregateFunctionResult = graphParams.aggregateFunctionResult;
                vm.XAxisField = graphParams.selectedChartXAxis;
                vm.YAxisField = graphParams.selectedChartYAxis;
                vm.chartSeries = graphParams.chartseries;
                vm.chartOptions = graphParams.chartoptions;
                vm.XAxisFieldName = graphParams.XAxisFieldName;
                vm.YAXisFieldName = graphParams.YAXisFieldName;
                vm.ExtraFieldId = graphParams.ExtraFieldId
                vm.BindGraph = BindGraph;
            }
            else {
                vm.showChart = false;
            }

        },
        FilterCustomFieldName(CustomFieldId) {
            var vm = this;
            let CustomFieldName;
            for (let [index] of vm.Columnlist.entries()) {
                if (vm.Columnlist[index].CustomFieldId == CustomFieldId || vm.Columnlist[index].FieldName == CustomFieldId) {
                    if (vm.Columnlist[index].DisplayName != null && vm.Columnlist[index].DisplayName != '') {
                        CustomFieldName = vm.Columnlist[index].DisplayName;
                    }
                    else {
                        CustomFieldName = vm.Columnlist[index].FieldName;
                    }
                }
            }
            return CustomFieldName;
        },
        CancelAll: function () {
            this.$router.push({ name: 'CustomReports' });
        },
        CheckColumnExistInGraph: function (CustomFieldId) {
            var vm = this;
            var expression = true;
            if (vm.XAxisField != CustomFieldId && vm.YAxisField != CustomFieldId) {
                return !expression;
            }
            return expression;

        },
        GetAllRecordsForPDF: async function (excelvalue) {
            debugger
            var vm = this;
            vm.count = 0;
            if (vm.previewdata.length > 0) {
                var temp = {
                    "Info": {
                        "TYPE": vm.saveJsonReport.Info.TYPE,
                        "REPORTID": vm.customreportparameters.ReportID,
                        "REPORTNAME": vm.ReportName,
                        "MODULENAME": vm.customreportparameters.Modulename,
                        "SUBMODULECODE": vm.customreportparameters.Submodulecode,
                        "SUBMODULEID": vm.customreportparameters.submoduleid,
                        "PAGESIZE": vm.PageSize,
                        "PAGENUMBER": parseInt(vm.PageNumber),
                        "IS_PAGED": 1,
                        "IsForPreview": vm.isPreviewForPdfExcel,
                        "FIELDS": vm.saveJsonReport.Info.FIELDS.length > 0 ? vm.saveJsonReport.Info.FIELDS : vm.PdfJsonReport.Info.FIELDS,
                        "FILTERS": vm.saveJsonReport.Info.FILTERS.length > 0 ? vm.saveJsonReport.Info.FILTERS : vm.PdfJsonReport.Info.FILTERS,
                        "GROUP": vm.saveJsonReport.Info.GROUP.length > 0 ? vm.saveJsonReport.Info.GROUP : vm.PdfJsonReport.Info.GROUP,
                        "SORTING": vm.saveJsonReport.Info.SORTING.length > 0 ? vm.saveJsonReport.Info.SORTING : vm.PdfJsonReport.Info.SORTING,
                        "EXPRESSION": vm.saveJsonReport.Info.EXPRESSION.length > 0 ? vm.saveJsonReport.Info.EXPRESSION : vm.PdfJsonReport.Info.EXPRESSION,
                        "FromDate": null,
                        "ToDate": null,
                        "PIVOTROW": vm.saveJsonReport.Info.PIVOTROW ? vm.saveJsonReport.Info.PIVOTROW : [],
                        "PIVOTCOLUMN": vm.saveJsonReport.Info.PIVOTCOLUMN ? vm.saveJsonReport.Info.PIVOTCOLUMN : [],
                        "PIVOTValues": vm.saveJsonReport.Info.PIVOTValues ? vm.saveJsonReport.Info.PIVOTValues : []

                    }
                }
                var jsondata = JSON.stringify(temp);
                vm.ExcelJsonData = jsondata;
                if (excelvalue == undefined) {
                    await ReportPreviewService.PreviewReport(jsondata).then(async (response) => {
                        if (response.data != null && response.data != undefined) {
                            vm.allReocrdsData = JSON.parse(response.data[0].Value);
                            vm.count = vm.count + 1;
                        }
                    }).catch((err) => {
                        console.log(err);
                    })
                }
            }
        },
        //Export TO PDF functionality 
        ExportToPDF: async function () {
            var vm = this;
            vm.isLoading = true;
            await vm.GetAllRecordsForPDF();
            if (vm.count != 0) {

                if (vm.chartOptions.legend != undefined) {
                    vm.chartOptions.legend.width = screen.availWidth - screen.availWidth * 69 / 100;
                }
                var graphURI = await vm.GetGraphImageURI();
                var GraphData = {};
                var allRecordslisting = vm.allReocrdsData.ListingData;
                let MetaData = vm.allReocrdsData.MetaData[0];
                let ReportName = vm.ReportName.replace('.', '-');
                let pageType = 'A2';
                let pdfDetails;
                let pageOrientation = '';
                let GraphWidth = 300;

                // Create PDF with  Grouping 
                if (MetaData.IsGrouping) {
                    await vm.BindPDFDataForGroup(allRecordslisting);
                }
                // Create PDF without  Grouping 
                else {
                    await vm.BindPDFData(allRecordslisting);
                }

                //Set Page Type 
                if (vm.TableHeader.length > 5) {
                    pageType = "A1";
                    GraphWidth = 500
                }
                //Set Page  pageOrientation

                if (vm.TableHeader.length > 10) {
                    pageOrientation = 'landscape';
                }
                //Add graph Data
                if (graphURI != null && graphURI != undefined) {
                    GraphData = {
                        image: graphURI.imgURI,
                        width: 500,
                        alignment: 'center'
                    }
                }

                pdfDetails = {
                    pageSize: pageType,
                    pageOrientation: pageOrientation,
                    content: [
                        GraphData,
                        { text: ` ${ReportName} `, style: 'subheader' },
                        {
                            style: 'table',
                            layout: {
                                fillColor: function (rowIndex) {
                                    return (rowIndex === 0) ? '#CCCCCC' : null;
                                },
                                hLineWidth: function () { return 0.5; },
                                vLineWidth: function () { return 0.5; },
                            },
                            table: {
                                headerRows: 1,
                                body: vm.TableBody,
                                widths: vm.TableHeader.length > 10
                                    ? Array(vm.TableHeader.length).fill('auto')
                                    : Array(vm.TableHeader.length).fill('*')
                            }
                        }
                    ],
                    styles: vm.SetPDFStyle(),
                    defaultStyle: {
                        //alignment: 'justify'
                        fontSize: 6, // Reduce font size
                        alignment: 'left'
                    }
                }
                pdfMake.createPdf(pdfDetails).download(ReportName);
                vm.isLoading = false;
            } else {
                vm.ShowAlert(vm.$t("No_Record_Found"), 'failure', true, vm.$t('Alert'));
                vm.isLoading = false;
            }
        },

        BindPDFData: function (allRecordslisting) {
            var vm = this;
            vm.TableBody = [];
            vm.TableHeader = [];
            var TableHeader2 = [];
            //Add  Column for Table header
            var ColumnList = Object.keys(allRecordslisting[0])
            ColumnList.forEach((item) => {
                if (item != 'ROWNUMBER') {
                    vm.TableHeader.push({ text: vm.$t(item), style: 'tableHeader', alignment: 'center' })
                    TableHeader2.push({ text: item, style: 'tableHeader', alignment: 'center' })
                }
            })

            vm.TableBody.push(vm.TableHeader);

            allRecordslisting.forEach((item) => {
                let currentData = [];
                TableHeader2.forEach((heading) => {
                    let pdfdata = null;
                    if (typeof item[heading.text] == 'string') {
                        pdfdata = item[heading.text].replace(/(<([^>]+)>)/ig, '');
                    } else { pdfdata = item[heading.text] }
                    currentData.push(pdfdata);
                })
                vm.TableBody.push(currentData);
            })

             if (vm.ReportType == '0') {

                let totalsRow = [];
                let totalsData = vm.allReocrdsData.AggregateTotalsColumnWise?.[0] || {}; // safety fallback

                TableHeader2.forEach((heading) => {
                    const colKey = heading.text;
                    if (totalsData.hasOwnProperty(colKey)) {
                        totalsRow.push({ text: totalsData[colKey].toLocaleString(), bold: true, alignment: 'left' });
                    } else {
                        totalsRow.push({ text: '', bold: true });
                    }
                });

                // Add totals row with label if needed
                if (totalsRow.length > 0) {
                    // Optional: Replace first column with "Total" label
                    totalsRow[0] = { text: vm.$t("Overall Total"), bold: true, alignment: 'left' };
                    vm.TableBody.push(totalsRow);
                }
            }
        },

        BindPDFDataForGroup: function (allRecordslisting) {
            var vm = this;
            vm.TableBody = [];
            vm.TableHeader = [];
            var TableHeader2 = [];
            //Add Grouping Column for Table header
            vm.TableHeader.push({ text: vm.$t(Object.keys(allRecordslisting[0])[0]), style: 'tableHeader', alignment: 'center' })
            TableHeader2.push({ text: Object.keys(allRecordslisting[0])[0], style: 'tableHeader', alignment: 'center' })
            // Add all Column in Table Header 
            var columnList = Object.keys(allRecordslisting[0].data[0])
            columnList.forEach((item) => {
                vm.TableHeader.push({ text: vm.$t(item), style: 'tableHeader', alignment: 'center' })
                TableHeader2.push({ text: item, style: 'tableHeader', alignment: 'center' })
            })

            //Add First header array into Table Body
            vm.TableBody.push(vm.TableHeader);

            //Create Array for PDF
            allRecordslisting.forEach((item) => {
                item.data.forEach((heading, groupingindex) => {
                    let tableData = [];
                    TableHeader2.forEach((headerelement, headerindex) => {
                        if (groupingindex == 0 && headerindex == 0) {
                            let pdfgroupdata = null;
                            if (typeof item[headerelement.text] == 'string') {
                                pdfgroupdata = item[headerelement.text].replace(/(<([^>]+)>)/ig, '');
                            } else { pdfgroupdata = item[headerelement.text] }
                            var groupColumn = `${pdfgroupdata} (${item.totalRecord})`
                            tableData.push({ rowSpan: parseInt(item.totalRecord), text: groupColumn })
                        }
                        else if (groupingindex == 1 && headerindex == 0) {
                            tableData.push("")
                        }
                        else {
                            if (heading[headerelement.text] != undefined && heading[headerelement.text] != "") {
                                let pdfwithoutgroupdata = null;
                                if (typeof heading[headerelement.text] == 'string') {
                                    pdfwithoutgroupdata = heading[headerelement.text].replace(/(<([^>]+)>)/ig, '');
                                } else { pdfwithoutgroupdata = heading[headerelement.text] }
                                tableData.push(pdfwithoutgroupdata);
                            }
                            else {
                                var tempitem = heading[headerelement.text] = "-";
                                tableData.push(tempitem);
                            }
                        }
                    })
                    vm.TableBody.push(tableData)
                })
            })
        },
        SetPDFStyle: function () {
            return {
                subheader: {
                    fontSize: 16,
                    bold: true,
                    margin: [0, 0, 0, 10]
                },
                table: {
                    margin: [0, 5, 0, 15]
                }
            };
        },
        GetGraphImageURI: async function () {
            var vm = this;
            var URI;
            await vm.$refs.chart?.chart.dataURI().then(uri => {
                URI = uri;
            });
            return URI;
        },
        ExportToExcel: async function () {
            var vm = this;
            vm.isLoading = true;
            // var jsonData = JSON.stringify(vm.saveJsonReport.Info.GROUP.length > 0 ? vm.saveJsonReport : vm.PdfJsonReport);
            await vm.GetAllRecordsForPDF('Excel');
            if (vm.ExcelJsonData != null && vm.ExcelJsonData != undefined) {
                var jsonData = vm.ExcelJsonData;
                var params = `reportId=${vm.customreportparameters.ReportID}&pagetype=preview&IsPaged=1`;
                ReportPreviewService.GetExcelReport(params, jsonData).then((response) => {
                    var blob = new Blob([response.data], { type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;' });
                    var link = document.createElement('a');
                    link.href = window.URL.createObjectURL(blob);
                    const current = new Date();
                    const date = `${current.getDate()}_${current.getMonth() + 1}_${current.getFullYear()}`;
                    var fileName = `ReportData-${vm.ReportName.replace('.', '-')}-${date}`;
                    fileName = fileName.split(" ").join("");
                    link.download = fileName;
                    link.click();
                    vm.isLoading = false
                }).catch((err) => { console.log(err); vm.isLoading = false; })
            } else {
                vm.ShowAlert(vm.$t("No_Record_Found"), 'failure', true, vm.$t('Alert'));
                vm.isLoading = false;
            }
        },
        close() {
            var vm = this;
            vm.isSortPopup = false;
        }
    }
}
</script>

